import { Component, OnInit } from '@angular/core';
import { FakeDataService } from './../../services/fake-data.service'
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-restaurant',
  templateUrl: './restaurant.page.html',
  styleUrls: ['./restaurant.page.scss'],
})
export class RestaurantPage implements OnInit {
  private categories: any = [];
  private plates: any = [];
  private platesCopy: any = [];

  private backgroundImage: string ="https://images.pexels.com/photos/461428/pexels-photo-461428.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500";
  private avatarImage: string ="https://images.pexels.com/photos/220453/pexels-photo-220453.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500";

  private id: string = null;

  private selectedCategory: number = 1;

  constructor(private fds: FakeDataService, private route: ActivatedRoute) {
    this.id = this.route.snapshot.paramMap.get('id');

    const { categories, plates, avatarImage, backgroundImage } = this.fds.getRestaurant(this.id);

    this.categories = categories;
    this.plates = plates;
    this.platesCopy = this.plates;

    if (avatarImage && backgroundImage) {
      this.avatarImage = avatarImage;
      this.backgroundImage = backgroundImage;
    }
  }

  ngOnInit() {
  }

  segmentChanged(ev: any) {
    const { detail: { value } } = ev;

    this.selectedCategory = value;

    if (value != 1) {
      this.platesCopy = this.plates.filter((plate) => plate.categoryId == this.selectedCategory);
    } else {
      this.platesCopy = this.plates;
    }
  }
}
